let socketTask = null;
let reconnectTimer = null;
let reconnectCount = 0;
const MAX_RECONNECT = 6;

let currentUserId = null;
let messageQueue = [];
const QUEUE_KEY = 'socket_message_queue';
let onReadAck = null;

const msgStatusCallbacks = new Map();
const ackTimers = new Map();

const CONNECT_STATUS = {
    DISCONNECTED: 0,
    CONNECTING: 1,
    CONNECTED: 2,
};
let connectStatus = CONNECT_STATUS.DISCONNECTED;

export function connectSocket(userId, onMessage) {
    if (connectStatus === CONNECT_STATUS.CONNECTED || connectStatus === CONNECT_STATUS.CONNECTING) {
        console.warn('WebSocket Â∑≤ÁªèËøûÊé•ÊàñÊ≠£Âú®ËøûÊé•‰∏≠ÔºåË∑≥ËøáÈáçÂ§çËøûÊé•');
        return;
    }

    currentUserId = userId;
    connectStatus = CONNECT_STATUS.CONNECTING;
    console.log('[socket] ÂáÜÂ§áËøûÊé• WebSocketÔºåÁî®Êà∑ID:', userId);

    const wsUrl = `ws://192.168.110.238:9326?name=${encodeURIComponent(userId)}`;

    try {
        socketTask = uni.connectSocket({
            url: wsUrl,
            success() { console.log('WebSocket ËøûÊé•ËØ∑Ê±ÇÂ∑≤ÂèëËµ∑'); },
            fail(err) { console.error('WebSocket ËøûÊé•ËØ∑Ê±ÇÂ§±Ë¥•', err); attemptReconnect(onMessage); },
        });
    } catch (e) {
        console.error('WebSocket ËøûÊé•ÂºÇÂ∏∏', e);
        attemptReconnect(onMessage);
        return;
    }

    socketTask.onOpen(() => {
        console.log('üì° WebSocket Â∑≤ÊâìÂºÄ');
        connectStatus = CONNECT_STATUS.CONNECTED;
        reconnectCount = 0;

        const loginData = { cmd: 1, from: currentUserId };
        sendRaw(loginData);

        loadQueueFromStorage();
        autoSendOfflineReadAck();
        flushQueue(onMessage);
    });

    socketTask.onMessage((res) => {
        const dataStr = res.data;
        if (!dataStr || dataStr === 'null' || dataStr === 'undefined') return;

        try {
            const data = JSON.parse(dataStr);

            // Â∑≤ËØªÂõûÊâß 101 Âè™Êõ¥Êñ∞ÂèëÈÄÅÊñπ
            if (data.cmd === 101 && data.msgIds && Array.isArray(data.msgIds)) {
                if (currentUserId === data.from) {
                    onReadAck && onReadAck(data.msgIds);
                }
            }
            // ACKÁ°ÆËÆ§ -1
            else if (data.cmd === -1 && data.msgId) {
                const cb = msgStatusCallbacks.get(data.msgId);
                if (cb) { cb('success'); msgStatusCallbacks.delete(data.msgId); }
                if (ackTimers.has(data.msgId)) { clearTimeout(ackTimers.get(data.msgId)); ackTimers.delete(data.msgId); }
            }
            // ÊôÆÈÄöÊ∂àÊÅØÂ§ÑÁêÜ
            else {
                onMessage && onMessage(data);
                if (data.msgId && data.cmd !== 101 && data.cmd !== -1) sendAck(data.msgId);
            }
        } catch (e) {
            console.error('Ê∂àÊÅØËß£ÊûêÈîôËØØ', e, dataStr);
        }
    });

    socketTask.onClose(() => {
        console.log('WebSocket Â∑≤ÂÖ≥Èó≠');
        connectStatus = CONNECT_STATUS.DISCONNECTED;
        attemptReconnect(onMessage);
    });

    socketTask.onError((err) => {
        console.error('WebSocket ÈîôËØØ', err);
        connectStatus = CONNECT_STATUS.DISCONNECTED;
        attemptReconnect(onMessage);
    });
}

// Ëá™Âä®ÂèëÈÄÅÁ¶ªÁ∫øÊ∂àÊÅØÂ∑≤ËØªÂõûÊâßÔºåÂè™ÂèëÈÄÅÊé•Êî∂ÊñπÊ∂àÊÅØ
function autoSendOfflineReadAck() {
    if (!messageQueue.length) return;
    const offlineMsgIds = messageQueue
        .filter(item => item.msgId && item.from !== currentUserId)
        .map(item => item.msgId);
    if (offlineMsgIds.length > 0) sendReadAck(offlineMsgIds);
}

function generateUUID() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
        const r = Math.random() * 16 | 0,
            v = c === 'x' ? r : (r & 0x3 | 0x8);
        return v.toString(16);
    });
}

function persistQueue() {
    try {
        uni.setStorageSync(QUEUE_KEY, messageQueue);
        console.log('[socket] ÁºìÂ≠òÈòüÂàóÂ∑≤ÊåÅ‰πÖÂåñÔºåÈïøÂ∫¶:', messageQueue.length);
    } catch (e) {
        console.error('[socket] persistQueue error', e);
    }
}

function loadQueueFromStorage() {
    try {
        const q = uni.getStorageSync(QUEUE_KEY);
        if (Array.isArray(q)) {
            messageQueue = q;
        } else {
            messageQueue = [];
        }
        messageQueue = messageQueue.filter(item => {
            return !(item && (item.cmd === 99 || item.cmd === 100));
        });
        console.log('[socket] ‰ªéÊú¨Âú∞ÁºìÂ≠òÊÅ¢Â§çÈòüÂàóÔºåÈïøÂ∫¶:', messageQueue.length);
    } catch (e) {
        console.error('[socket] ‰ªéÊú¨Âú∞ÁºìÂ≠òÊÅ¢Â§çÈòüÂàóÂºÇÂ∏∏', e);
        messageQueue = [];
    }
}

function attemptReconnect(onMessage) {
    if (reconnectCount >= MAX_RECONNECT) {
        console.warn('ÈáçËøûÊ¨°Êï∞ËææÂà∞‰∏äÈôêÔºåÂÅúÊ≠¢ÈáçËøû');
        return;
    }
    if (reconnectTimer) return;

    reconnectCount++;
    const delay = Math.min(30000, 5000 * Math.pow(2, reconnectCount - 1));
    console.log(`Á¨¨${reconnectCount}Ê¨°ÈáçËøûÔºå${delay}msÂêéÂ∞ùËØï`);

    reconnectTimer = setTimeout(() => {
        reconnectTimer = null;
        connectSocket(currentUserId, onMessage);
    }, delay);
}

function sendRaw(data) {
    if (socketTask && connectStatus === CONNECT_STATUS.CONNECTED) {
        try {
            socketTask.send({ data: JSON.stringify(data) });
        } catch (e) {
            console.error('[socket] ÂèëÈÄÅÊ∂àÊÅØÂºÇÂ∏∏', e, data);
        }
    } else {
        console.warn('[socket] WebSocketÊú™ËøûÊé•ÔºåÊó†Ê≥ïÂèëÈÄÅÊ∂àÊÅØ:', data);
    }
}

function flushQueue(onMessage) {
    if (!messageQueue.length) {
        console.log('[socket] flushQueueÔºöÊó†ÁºìÂ≠òÊ∂àÊÅØÈúÄË¶ÅÂèëÈÄÅ');
        return;
    }
    console.log('[socket] flushQueue start, Ê∂àÊÅØÊï∞Èáè:', messageQueue.length);

    const sendNext = () => {
        if (!messageQueue.length) {
            console.log('[socket] flushQueue ÂÆåÊàêÔºåÁºìÂ≠òÈòüÂàóÊ∏ÖÁ©∫');
            persistQueue();
            return;
        }
        const item = messageQueue[0];
        try {
            socketTask.send({
                data: JSON.stringify(item),
                success() {
                    console.log('[socket] flushQueue ÂèëÈÄÅÊàêÂäü:', item);
                    setupAckTimeout(item.msgId);
                    messageQueue.shift();
                    persistQueue();
                    setTimeout(sendNext, 50);
                },
                fail(err) {
                    console.warn('[socket] flushQueue ÂèëÈÄÅÂ§±Ë¥•', err);
                    // ‰∏çË∞ÉÁî®Áä∂ÊÄÅÂõûË∞ÉÔºåÁ≠âÂæÖ‰∏ã‰∏ÄÊ¨°flushÈáçËØï
                },
            });
        } catch (e) {
            console.error('[socket] flushQueue ÂºÇÂ∏∏', e);
        }
    };
    sendNext();
}

function setupAckTimeout(msgId) {
    if (!msgId) return;
    if (ackTimers.has(msgId)) {
        clearTimeout(ackTimers.get(msgId));
    }
    const timerId = setTimeout(() => {
        const cb = msgStatusCallbacks.get(msgId);
        if (cb) {
            cb('failed');
            msgStatusCallbacks.delete(msgId);
        }
        ackTimers.delete(msgId);
    }, 5000);
    ackTimers.set(msgId, timerId);
}

function sendData(data, onStatusChange) {
    if (!data.msgId) {
        data.msgId = generateUUID();
    }
    if (onStatusChange && typeof onStatusChange === 'function') {
        msgStatusCallbacks.set(data.msgId, onStatusChange);
    }
    if (onStatusChange) onStatusChange('sending');

    if (connectStatus !== CONNECT_STATUS.CONNECTED || !socketTask) {
        console.warn('WebSocket Êú™ËøûÊé•ÔºåÊ∂àÊÅØÂä†ÂÖ•ÈòüÂàóÁºìÂ≠ò', data);
        messageQueue.push(data);
        persistQueue();
        if (onStatusChange) onStatusChange('failed');
        return;
    }

    try {
        socketTask.send({
            data: JSON.stringify(data),
            success() {
                console.log('[socket] Ê∂àÊÅØÂèëÈÄÅÊàêÂäü', data);
                setupAckTimeout(data.msgId);
            },
            fail(err) {
                console.error('ÂèëÈÄÅÊ∂àÊÅØÂ§±Ë¥•ÔºåÂä†ÂÖ•ÁºìÂ≠ò', err, data);
                messageQueue.push(data);
                persistQueue();
                if (onStatusChange) onStatusChange('failed');
            },
        });
    } catch (e) {
        console.error('ÂèëÈÄÅÊ∂àÊÅØÂºÇÂ∏∏ÔºåÊ∂àÊÅØÂä†ÂÖ•ÁºìÂ≠ò', e, data);
        messageQueue.push(data);
        persistQueue();
        if (onStatusChange) onStatusChange('failed');
    }
}

export function sendMsg(toUserId, msg, fromUserId, onStatusChange) {
    const data = {
        cmd: 2,
        type: 'private',
        from: fromUserId,
        to: toUserId,
        message: msg,
        timestamp: Date.now(),
    };
    sendData(data, onStatusChange);
}

export function sendGroupMsg(groupId, msg, fromUserId, onStatusChange) {
    const data = {
        cmd: 3,
        type: 'group',
        from: fromUserId,
        to: groupId,
        message: msg,
        timestamp: Date.now(),
    };
    sendData(data, onStatusChange);
}

export function retrySend(msgObj, onStatusChange) {
    if (!msgObj.msgId) {
        console.warn('retrySend Áº∫Â∞ë msgIdÔºåÊó†Ê≥ïÈáçÂèë');
        return;
    }
    console.log('[socket] retrySend ÈáçÂèëÊ∂àÊÅØ', msgObj);
    sendData(msgObj, onStatusChange);
}

function sendAck(msgId) {
    if (socketTask && connectStatus === CONNECT_STATUS.CONNECTED) {
        const ackData = {
            cmd: 99,
            msgId: msgId,
        };
        try {
            socketTask.send({ data: JSON.stringify(ackData) });
            console.log('[socket] ÂèëÈÄÅACKÁ°ÆËÆ§Ê∂àÊÅØ:', ackData);
        } catch (e) {
            console.error('[socket] ÂèëÈÄÅACKÊ∂àÊÅØÂ§±Ë¥•', e);
        }
    }
}

export function sendReadAck(msgIds) {
    if (!Array.isArray(msgIds) || msgIds.length === 0) {
        console.warn('[socket] sendReadAck Áº∫Â∞ë msgIds');
        return;
    }
    if (socketTask && connectStatus === CONNECT_STATUS.CONNECTED) {
        const ackData = {
            cmd: 100,
            msgIds: msgIds
        };
        try {
            socketTask.send({ data: JSON.stringify(ackData) });
            console.log('[socket] ÂèëÈÄÅÂ∑≤ËØªÁ°ÆËÆ§:', ackData);
        } catch (e) {
            console.error('[socket] ÂèëÈÄÅÂ∑≤ËØªÁ°ÆËÆ§Â§±Ë¥•', e);
        }
    }
}

export function setReadAckHandler(callback) {
    onReadAck = callback;
}

export function closeSocket() {
    if (socketTask) {
        console.log('[socket] ‰∏ªÂä®ÂÖ≥Èó≠ WebSocket ËøûÊé•');
        socketTask.close();
        socketTask = null;
        connectStatus = CONNECT_STATUS.DISCONNECTED;
        if (reconnectTimer) {
            clearTimeout(reconnectTimer);
            reconnectTimer = null;
        }
        reconnectCount = 0;
    }
}

export function isConnected() {
    return connectStatus === CONNECT_STATUS.CONNECTED;
}